import backTop from '@/components/common/back-top.vue'
import popup from '@/components/common/popup.vue'
import { mapState, mapMutations, mapActions } from 'vuex'
import product from '@/static/data/product.js'
import count from '@/components/common/count.vue'
export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: false,
			interval: 2000,
			duration: 500,
			isShowTop: false,
			isShowPopup: false,
			detail: {},
			animationData: {},
			typeId: 0,
			goodsId: 0,
		}
	},
	computed: {
		...mapState([
			'cartList'
		])
	},
	methods: {
		...mapMutations(['changeCartList']),
		toPage() {
			uni.navigateTo({
				url: '../../pages/index/index'
			})
		},
		toCart() {
			uni.navigateTo({
				url: '../../pages/shopcart/index',
			})
		},
		backPre() {
			uni.navigateBack({
				delta: 1
			})
		},
		buy() {
			this.isShowPopup = true;
			let timer = setTimeout(()=>{
				this.isShowPopup = false;
			},1200);
			let cartItem = {
				typeId: this.typeId,
				goodsId: this.goodsId,
				num: 1
			};
			this.changeCartList(cartItem);
		}
	},
	onPageScroll(e) {
		if(e.scrollTop > 500) {
			this.isShowTop = true;
		}else{
			this.isShowTop = false;
		}
	},
	onLoad(option) {
		// console.log(option.typeId,option.goodsId);
		this.typeId = option.typeId;
		this.goodsId = option.goodsId;
		
		
		product.forEach((value,index)=>{
			if(value.typeId == option.typeId){
				let arr = value.goodsList;
				arr.forEach((val,ind)=>{
					if(val.goodsId == option.goodsId){
						this.detail = val;
					}
				})
			}
		})
	},
	onShow() {
		var animation = uni.createAnimation({
			duration: 1000,
			timingFunction: 'ease',
		})
		this.animation = animation;
		animation.translateY(-80).step();
		this.animationData = animation.export()
	},
	components: {
		backTop,
		popup,
		count,
	}
}
